import React from "react";
import Header from "../components/header";
import {Link} from "react-router-dom";
import '../stylesheets/chart.css';
import Footer from "../components/footer";
import List from "../components/chartList";
import Update from "../components/update";
import PlayURL from "../components/getPlaylist";


class ChartHour extends React.Component {
    render() {
        return (
            <div className="container">
                <Header/>
                <hr className="chart-menu-separate"/>
                <div className="chart-menu">
                    <Link to="/charts" className="chart-menu-text">TOP100</Link>
                    <Link to="/charts/hourly" className="chart-menu-text selected">시간</Link>
                    <Link to="/charts/daily" className="chart-menu-text">일간</Link>
                    <Link to="/charts/weekly" className="chart-menu-text">주간</Link>
                    <Link to="/charts/monthly" className="chart-menu-text">월간</Link>
                </div>
                <hr className="chart-menu-separate"/>
                <div className="fade-in-page">
                    <div className="chart-type">시간</div>
                    <Update type="hourly"/>
                    <PlayURL type="hourly"/>
                    <hr className="section-separate"/>
                    <div className="chart-wrap">
                        <div className="chart-header">
                            <span id="header-pos">순위</span>
                            <span id="header-title">제목</span>
                            <span id="header-artist">가수</span>
                            <div className="header-right-align-chart">
                                <span id="header-peak">1시간전</span>
                                <span id="header-date">날짜</span>
                                <span id="header-views">조회수</span>
                            </div>
                        </div>
                        <hr className="chart-separate-line"/>
                        <List type="hourly" limit={100}/>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

export default ChartHour;