import React, {useEffect} from "react";
import {Link, useLocation} from "react-router-dom";
import logo from '../images/logo.png';
import '../stylesheets/index.css';
import SearchBar from "./searchBar";


function Header() {
    const location = useLocation();

    useEffect(() => {
        const paths = location.pathname.split('/');
        let id = paths[1];
        if (!id) return;
        const elements = document.getElementsByClassName('menu-item');
        const element = document.getElementById(id);

        for (let i = 0; i < elements.length; i++) {
            elements[i].className = "menu-item";
            if (elements[i] === element) elements[i].className += " menu-selected";
        }
    }, [location.pathname]);

    function openMenu() {
        const burger = document.getElementById('burger');
        const slide = document.getElementById('menu-slide');
        if (burger.className.includes(' on')) {
            burger.className = 'burger';
            slide.className = 'menu-slide'
        } else {
            burger.className = 'burger on';
            slide.className = 'menu-slide on';
        }
    }

    return (
        <div className="header">
            <Link to="/"><img src={logo} alt="logo" id="header-logo"/></Link>
            <SearchBar/>
            <div className="menu-vertical"/>
            <div className="menu">
                <Link to="/charts" className="menu-item" id="charts">왁론차트</Link>
                <Link to="/new" className="menu-item" id="new">최신음악</Link>
                <Link to="/tags" className="menu-item" id="tags">태그별 음악</Link>
                <a href="https://billboardoo.com" target="_blank" rel="noreferrer" className="menu-item">빌보두</a>
                <Link to="/credit" className="menu-item" id="credit">개발자</Link>
            </div>
            <div className="mobile-menu">
                <div className="mob-menu" id="mob-menu" onClick={openMenu}>
                    <span className="burger" id="burger">
                        <span className="top-line"/>
                        <span className="bot-line"/>
                    </span>
                </div>

                <div className="menu-slide" id="menu-slide">
                    <img src={logo} alt="logo" className="mobile-logo"/>
                    <ul>
                        <Link to="/">
                            <li>홈</li>
                        </Link>
                        <Link to="/charts">
                            <li>왁론차트</li>
                        </Link>
                        <Link to="/new">
                            <li>최신음악</li>
                        </Link>
                        <Link to="/tags">
                            <li>태그별 음악</li>
                        </Link>
                        <a href="https://billboardoo.com" target="_blank" rel="noreferrer">
                            <li>빌보두</li>
                        </a>
                        <Link to="/credit">
                            <li>개발자</li>
                        </Link>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Header;