import React from "react";
import Header from "../components/header";
import {Link} from "react-router-dom";
import '../stylesheets/genre.css';
import Footer from "../components/footer";
import TagMusic from "../components/tagMusic";


class Tag extends React.Component {
    render() {
        return (
            <div className="container">
                <Header/>
                <hr className="chart-menu-separate"/>
                <div className="fade-in-page">
                    <div className="new-section-title fade-in-page" id="tag-title">태그별 음악</div>
                    <div className="tags-wrap">
                        <Link to="/tags" className="t-name">
                            <div className="tag-name" id="all">전체</div>
                        </Link>
                        <Link to="/tags/vocaloid" className="t-name">
                            <div className="tag-name" id="vocaloid">왁컬로이두</div>
                        </Link>
                        <Link to="/tags/remix" className="t-name">
                            <div className="tag-name" id="remix">리믹스</div>
                        </Link>
                        <Link to="/tags/fanmade" className="t-name">
                            <div className="tag-name" id="fanmade">팬 노래</div>
                        </Link>
                        <Link to="/tags/anniversary" className="t-name">
                            <div className="tag-name" id="anniversary">기념곡</div>
                        </Link>
                        <Link to="/tags/mmd" className="t-name">
                            <div className="tag-name" id="mmd">MMD</div>
                        </Link>
                        <Link to="/tags/contest" className="t-name">
                            <div className="tag-name" id="contest">연말공모전</div>
                        </Link>
                    </div>
                    <TagMusic/>
                </div>
                <Footer/>
            </div>
        );
    }
}

export default Tag;