import React from "react";
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';
import Main from "./pages/main";
import Chart from "./pages/chart";
import ChartHour from "./pages/chartHour";
import ChartDay from "./pages/chartDay";
import ChartWeek from "./pages/chartWeek";
import ChartMonth from "./pages/chartMonth";
import New from "./pages/new";
import Credit from "./pages/credit";
import Tag from "./pages/tag";
import MusicPlayer from "./pages/player";


class App extends React.Component {
    render() {
        return (
            <div className="App">
                <Router>
                    <Routes>
                        <Route path="/" element={<Main/>}/>
                        <Route path="/charts" element={<Chart/>}/>
                        <Route path="/charts/hourly" element={<ChartHour/>}/>
                        <Route path="/charts/daily" element={<ChartDay/>}/>
                        <Route path="/charts/weekly" element={<ChartWeek/>}/>
                        <Route path="/charts/monthly" element={<ChartMonth/>}/>
                        <Route path="/new" element={<New/>}/>
                        <Route path="/credit" element={<Credit/>}/>
                        <Route path="/tags/*" element={<Tag/>}/>
                        <Route exact path="/player" element={<Main/>}/>
                        <Route path="/player/:id" element={<MusicPlayer/>}/>
                        {/*<Route path="/awards" element={<GomemFestival/>}/>*/}
                    </Routes>
                </Router>
            </div>
        );
    }
}

export default App;
