import React, {useEffect, useState} from "react";
import logo from '../images/logo.png';


function Footer() {
    const [wait, setWait] = useState(false);
    useEffect(() => {
        setTimeout(() => setWait(true), 700)
    })

    if (!wait) return null;
    return (
        <div className="footer fade-in-page">
            <img src={logo} alt="logo" id="footer-logo"/>
            <div id="footer-text"><b>빌보두왁론주식회사</b> 인천광역시 송도 왁엔터로
                158(민수플라자 25층 왁론차트개발부서) | 대표이사 우왁굳</div>
            <div id="footer-inform">본 사이트에 등장하는 모든 내용은 허구입니다.</div>
        </div>
    )
}

export default Footer;