import React, {useEffect, useState} from "react";
import {Link, useLocation} from "react-router-dom";
import axios from "axios";

function TagMusic() {
    const [data, setData] = useState([]);
    const location = useLocation();

    useEffect(() => {
        async function get() {
            const result = await axios.get(
                `/api/waklon/search${location.pathname === "/tags" ? "/tags/all" : location.pathname}`
            );
            setData(result.data);
        }

        get().then();

        const paths = location.pathname.split('/');
        let id = paths[paths.length - 1];
        if (id === "tags") id = "all";
        const elements = document.getElementsByClassName('tag-name');
        const element = document.getElementById(id);
        const container = document.getElementById('tag-container');

        for (let i = 0; i < elements.length; i++) {
            elements[i].className = "tag-name";
            if (elements[i] === element) elements[i].className += " tag-selected";
        }

        container.style = "opacity: 0;transition: 0.1s";
        setTimeout(() => container.style = "opacity: 1;transition: 0.5s", 300);

    }, [location.pathname]);

    function convertDate(date) {
        date = String(date);
        let year = date.slice(0, 2);
        let month = date.slice(2, 4);
        let day = date.slice(4, 6);
        return `20${year}.${month}.${day}`;
    }

    function addMusic() {
        let music = [];
        for (let i = 0; i < data.length; i++) {
            music.push(
                <Link to={`/player/${data[i].id}`} className="new-body fade-in-page">
                    <img src={data[i].image} alt="new img" className="new-image"/>
                    <div className="new-title">{data[i].title}</div>
                    <div className="new-artist">{data[i].artist}</div>
                    <div className="new-date">{convertDate(data[i].date)}</div>
                </Link>
            )
        }
        return music;
    }

    if (!data) return null;
    return <div id="tag-container" className="new-wrap">{addMusic()}</div>
}

export default TagMusic;