import React, {useState, useEffect} from "react";
import axios from "axios";
import Spinner from "./spinner";
import {Link} from "react-router-dom";


function NewAlbum(props) {
    const [data, setData] = useState([]);
    useEffect(() => {
        async function get() {
            const result = await axios.get(
                `/api/waklon/new/${props.maximum ? props.maximum * 5 : undefined}`
            );
            setData(result.data);
        }

        get().then();
    }, [props.maximum]);

    function convertDate(date) {
        date = String(date);
        let year = date.slice(0, 2);
        let month = date.slice(2, 4);
        let day = date.slice(4, 6);
        return `20${year}.${month}.${day}`;
    }

    function addNew() {
        let music = [];
        for (let i = 0; i < data.length; i++) {
            music.push(
                <Link to={`/player/${data[i].id}`} className="new-body fade-in-page">
                    <img src={data[i].image} alt="new img" className="new-image"/>
                    <div className="new-title">{data[i].title}</div>
                    <div className="new-artist">{data[i].artist}</div>
                    <div className="new-date">{convertDate(data[i].date)}</div>
                </Link>
            )
        }
        return music;
    }

    if (!data) return <Spinner/>;
    return (
        <div className="new-wrap">{addNew()}</div>
    )
}

export default NewAlbum;